// src/ThemeToggle.jsx
import React, { useEffect, useState } from "react";

function setArcadeTheme(nextOn) {
  const on = Boolean(nextOn);
  document.body.classList.toggle("theme-arcade", on);
  try { localStorage.setItem("ssTheme", on ? "arcade" : "classic"); } catch {}
  // let the whole app know
  window.dispatchEvent(new CustomEvent("ss-theme-change", { detail: { arcade: on } }));
}

function getArcadeTheme() {
  try {
    const ls = localStorage.getItem("ssTheme");
    if (ls === "arcade" || ls === "classic") {
      return ls === "arcade";
    }
  } catch {}
  return document.body.classList.contains("theme-arcade");
}

export default function ThemeToggle({ onChange }) {
  const [arcade, setArcade] = useState(getArcadeTheme);

  // stay in sync if something else flips the theme (HUD button, etc.)
  useEffect(() => {
    const sync = (val) => {
      const on = typeof val === "boolean" ? val : getArcadeTheme();
      setArcade(on);
      onChange?.(on);
    };

    const onEvt = (e) => sync(!!e.detail?.arcade);
    const onStorage = () => sync(); // when another tab changes it
    const mo = new MutationObserver(() => sync());

    window.addEventListener("ss-theme-change", onEvt);
    window.addEventListener("storage", onStorage);
    mo.observe(document.body, { attributes: true, attributeFilter: ["class"] });

    // initial normalize
    sync();

    return () => {
      window.removeEventListener("ss-theme-change", onEvt);
      window.removeEventListener("storage", onStorage);
      mo.disconnect();
    };
  }, [onChange]);

  const handleClick = () => {
    const next = !arcade;
    setArcadeTheme(next);
    setArcade(next);
    onChange?.(next);
  };

  return (
    <button className="btn" onClick={handleClick}>
      {arcade ? "Arcade off" : "Arcade on"}
    </button>
  );
}
