import "use-sync-external-store/shim";
import React, { useEffect, useState, useCallback, useRef } from "react";
import { useEditor, EditorContent } from "@tiptap/react";
import StarterKit from "@tiptap/starter-kit";
import Placeholder from "@tiptap/extension-placeholder";
import { Extension } from "@tiptap/core";
import { Plugin, PluginKey } from "prosemirror-state";
import { Decoration, DecorationSet } from "prosemirror-view";

import {
  analyzeSentence,
  countWords,
  countSentences,
  calculateFleschKincaidGrade,
  calculateFleschReadingEase,
} from "./readabilityUtils";
import { SIMPLE_WORD_MAP } from "./simpleWordMap";
import { useArcade } from "./ArcadeEngine";

/* ---------- config ---------- */
const ENABLE_BLUE = true;

const LONG_SENTENCE_WORDS = 25;
const HEMI_HARD_WORDS = 20;
const HEMI_VERY_HARD_WORDS = 25;
const DELTA_HARD = 1;
const MIN_YELLOW_WORDS_FOR_GRADE = 14;
const MIN_RED_WORDS_FOR_GRADE = 18;
const HARD_SYLLABLES_BY_TARGET = { 6: 3, 8: 4, 10: 5 };
const DEBOUNCE_MS = 250;
const MIN_WORDS_FOR_TARGET_BONUS = 20;

const POINTS = { red: 100, yellow: 50, blue: 25, hard: 25, target: 1000 };

const WORDY_PHRASES = [
  "due to the fact that","in order to","at this point in time","with regard to",
  "make use of","it is necessary to","for the purpose of"
];
const HEDGES = [
  "really","very","quite","basically","kind of","sort of","maybe","somewhat","fairly","rather",
  "likely","possibly","essentially","virtually","generally","probably"
];
const ADVERB_EXCEPTIONS = new Set(["family","only","belly","supply","Italy","reply","imply","apply"]);

/* ---------- utils ---------- */
const classify = (words, grade, target) => {
  const delta = grade - target;
  if (words >= HEMI_VERY_HARD_WORDS) return "red";
  if (words >= HEMI_HARD_WORDS) return "yellow";
  if (delta > 3 && words >= MIN_RED_WORDS_FOR_GRADE) return "red";
  if (delta > 1 && words >= MIN_YELLOW_WORDS_FOR_GRADE) return "yellow";
  if (ENABLE_BLUE && words >= LONG_SENTENCE_WORDS) return "blue";
  return null;
};

const smartCase = (orig, simple) => {
  if (!simple) return "";
  if (orig === orig.toUpperCase()) return simple.toUpperCase();
  if (orig[0] === orig[0].toUpperCase()) return simple[0].toUpperCase() + simple.slice(1);
  return simple;
};

const syllables = (word) => {
  const w = word.toLowerCase().replace(/[^a-z']/g, "");
  if (!w) return 0;
  const exceptions = { queue: 2, people: 2, business: 2, every: 2, family: 3, chocolate: 3 };
  if (w in exceptions) return exceptions[w];
  let count = 0;
  const vowels = "aeiouy";
  for (let i = 0; i < w.length; i++) {
    if (vowels.includes(w[i]) && !vowels.includes(w[i - 1] || "")) count++;
  }
  if (w.endsWith("e")) count--;
  return Math.max(1, count);
};

const isAllCaps = (w) => w.length > 1 && w === w.toUpperCase();
const isUrlLike = (w) => /^https?:\/\//i.test(w) || /^www\./i.test(w);
const isNumberLike = (w) => /^\d/.test(w);
const isLikelyProperNoun = (w) => /^[A-Z][a-z]/.test(w) && !isAllCaps(w);

const tooltipMessage = (color, { byLengthHard, byLengthYellow, byDeltaHard, byDeltaYellow }) => {
  if (color === "red") {
    if (byLengthHard && byDeltaHard) return "Very hard to read: long and above your target. Split and simplify.";
    if (byLengthHard) return "Very hard to read: 25+ words. Split this sentence.";
    if (byDeltaHard) return "Very hard to read: ~3 grades above target. Split and simplify.";
  }
  if (color === "yellow") {
    if (byLengthYellow && byDeltaYellow) return "Hard to read: a bit long and above target. Try a small rewrite.";
    if (byLengthYellow) return "Hard to read: 2024 words. Consider a split.";
    if (byDeltaYellow) return "Hard to read: ~1 grade above target. Try simpler words or a split.";
  }
  if (color === "blue") return "Long sentence. Consider a split.";
  return "";
};

const decoKey = new PluginKey("sentenceHighlights");
const overlaps = (aStart, aEnd, bStart, bEnd) => aStart < bEnd && bStart < aEnd;

/* ---------- decorations & counts ---------- */
function computeDecorationsAndCounts(doc, getTargetGrade, getShowHardWords, getEasySet) {
  const targetGrade = getTargetGrade();
  const showHardWords = getShowHardWords();
  const easySet = getEasySet();

  const decos = [];
  const counts = { red: 0, yellow: 0, blue: 0, hard: 0 };

  doc.descendants((node, pos) => {
    const isBlock = ["paragraph", "list_item", "heading"].includes(node.type.name);
    if (!isBlock) return;

    const text = node.textContent;
    if (!text) return;

    const blockFrom = pos + 1;
    const parts = text.match(/[^.!?]+[.!?]*/g) || [];
    let searchFrom = 0;

    for (const raw of parts) {
      const s = raw.trim();
      if (!s) { searchFrom += raw.length; continue; }

      const { wordCount, grade } = analyzeSentence(s);
      const color = classify(wordCount, grade, targetGrade);

      const startInNode = text.indexOf(s, searchFrom);
      if (startInNode === -1) { searchFrom += raw.length; continue; }

      const from = blockFrom + startInNode;
      const to = from + s.length;

      const delta = grade - targetGrade;
      const byLengthHard = wordCount >= HEMI_VERY_HARD_WORDS;
      const byLengthYellow = wordCount >= HEMI_HARD_WORDS;
      const byDeltaHard = delta > 3 && wordCount >= MIN_RED_WORDS_FOR_GRADE;
      const byDeltaYellow = delta > 1 && wordCount >= MIN_YELLOW_WORDS_FOR_GRADE;

      if (color) {
        counts[color] += 1;
        const tip = tooltipMessage(color, { byLengthHard, byLengthYellow, byDeltaHard, byDeltaYellow });
        decos.push(Decoration.inline(from, to, {
          nodeName: "mark",
          "data-highlight-color": color,
          "data-tip": tip || "",
        }));
      }

      const occupied = [];

      // hard words (DaleChall + syllable threshold)
      if (showHardWords) {
        const thresh = HARD_SYLLABLES_BY_TARGET[targetGrade] ?? 3;
        const wordRegex = /\b[\w'-]+\b/g;
        let m, tokenIndex = 0;
        while ((m = wordRegex.exec(s)) !== null) {
          const wOrig = m[0];
          const w = wOrig.replace(/^'+|'+$/g, "");
          const lower = w.toLowerCase();

          if (!w || isUrlLike(w) || isNumberLike(w)) { tokenIndex++; continue; }
          if (tokenIndex > 0 && isLikelyProperNoun(wOrig)) { tokenIndex++; continue; }
          if (isAllCaps(wOrig)) { tokenIndex++; continue; }

          const isEasy = easySet.has(lower);
          const syl = syllables(w);
          const isHardBySyll = syl >= thresh;

          if (!isEasy && isHardBySyll) {
            counts.hard += 1;
            const simple = SIMPLE_WORD_MAP[lower];
            const suggestion = simple ? smartCase(wOrig, simple) : null;
            const tip = suggestion
              ? `Likely hard word. Try ${suggestion}.`
              : "Likely hard word. Consider a simpler option.";
            const wFrom = from + m.index;
            const wTo = wFrom + wOrig.length;
            decos.push(Decoration.inline(wFrom, wTo, {
              "data-tip": tip,
              style: "text-decoration-line: underline; text-decoration-style: dotted; text-decoration-color: #7c3aed; text-decoration-thickness: 2px;",
            }));
            occupied.push([wFrom, wTo]);
          }
          tokenIndex++;
        }
      }

      // gray dashed: wordy phrases / passive / hedges / adverbs
      const sLower = s.toLowerCase();

      for (const phrase of WORDY_PHRASES) {
        let start = 0;
        while (true) {
          const i = sLower.indexOf(phrase, start);
          if (i === -1) break;
          const pFrom = from + i;
          const pTo = pFrom + phrase.length;
          if (!occupied.some(([a,b]) => overlaps(a,b,pFrom,pTo))) {
            decos.push(Decoration.inline(pFrom, pTo, {
              "data-tip": `Wordy phrase: ${phrase}`,
              style: "text-decoration-line: underline; text-decoration-style: dashed; text-decoration-color: #9aa3af; text-decoration-thickness: 1.5px;",
            }));
          }
          start = i + phrase.length;
        }
      }

      const passive = /\b(am|is|are|was|were|be|been|being)\s+(?:\w+ly\s+)?(\w+(?:ed|en))\b/gi;
      let pm;
      while ((pm = passive.exec(s)) !== null) {
        const part = pm[2];
        const relStart = pm.index + pm[0].lastIndexOf(part);
        const vFrom = from + relStart;
        const vTo = vFrom + part.length;
        decos.push(Decoration.inline(vFrom, vTo, {
          "data-tip": "Passive voice",
          style: "text-decoration-line: underline; text-decoration-style: dashed; text-decoration-color: #9aa3af; text-decoration-thickness: 1.5px;",
        }));
      }

      const hedgeWords = new Set(HEDGES.map(h => h.toLowerCase()));
      const wordRegex2 = /\b[\w'-]+\b/g;
      let m2;
      while ((m2 = wordRegex2.exec(s)) !== null) {
        const lower = m2[0].toLowerCase();
        if (hedgeWords.has(lower)) {
          const hFrom = from + m2.index;
          const hTo = hFrom + m2[0].length;
          decos.push(Decoration.inline(hFrom, hTo, {
            "data-tip": "Qualifier / hedge",
            style: "text-decoration-line: underline; text-decoration-style: dashed; text-decoration-color: #9aa3af; text-decoration-thickness: 1.5px;",
          }));
        }
      }

      const wordRegex3 = /\b[\w'-]+\b/g;
      let m3;
      while ((m3 = wordRegex3.exec(s)) !== null) {
        const wOrig = m3[0];
        const lower = wOrig.toLowerCase();
        if (lower.endsWith("ly") && wOrig.length > 3 && !ADVERB_EXCEPTIONS.has(wOrig)) {
          const aFrom = from + m3.index;
          const aTo = aFrom + wOrig.length;
          decos.push(Decoration.inline(aFrom, aTo, {
            "data-tip": "Adverb (-ly)",
            style: "text-decoration-line: underline; text-decoration-style: dashed; text-decoration-color: #9aa3af; text-decoration-thickness: 1.5px;",
          }));
        }
      }

      searchFrom = startInNode + s.length;
    }
  });

  return { decoSet: DecorationSet.create(doc, decos), counts };
}

/* ---------- extension with scoring ---------- */
function makeDynamicHighlightsExtension({ getTargetGrade, getShowHardWords, getEasySet, awardPointsRef }) {
  return Extension.create({
    name: "dynamicHighlights",
    addProseMirrorPlugins() {
      return [
        new Plugin({
          key: decoKey,
          state: {
            init: (_, state) => {
              const { decoSet, counts } = computeDecorationsAndCounts(
                state.doc, getTargetGrade, getShowHardWords, getEasySet
              );
              return { decoSet, counts };
            },
            apply: (tr, prev, _old, newState) => {
              const shouldRecompute = tr.docChanged || tr.getMeta(decoKey) === "recompute";
              if (!shouldRecompute) return prev;

              const { decoSet, counts } = computeDecorationsAndCounts(
                newState.doc, getTargetGrade, getShowHardWords, getEasySet
              );

              // Award when problems go DOWN
              if (tr.docChanged && awardPointsRef?.current) {
                const dRed  = Math.max(0, prev.counts.red    - counts.red);
                const dYel  = Math.max(0, prev.counts.yellow - counts.yellow);
                const dBlu  = Math.max(0, prev.counts.blue   - counts.blue);
                const dHard = Math.max(0, prev.counts.hard   - counts.hard);
                const deltaScore = dRed*POINTS.red + dYel*POINTS.yellow + dBlu*POINTS.blue + dHard*POINTS.hard;
                if (deltaScore > 0) awardPointsRef.current(deltaScore);
              }

              return { decoSet, counts };
            },
          },
          props: {
            decorations(state) {
              return decoKey.getState(state)?.decoSet || null;
            },
          },
        }),
      ];
    },
  });
}

/* ---------- component ---------- */
export default function TiptapEditor({ targetGrade }) {
  const { addScore } = useArcade();
  const [stats, setStats] = useState({ wordCount: 0, sentenceCount: 0, grade: 0, ease: 0 });
  const [showHardWords, setShowHardWords] = useState(() => {
    try { return localStorage.getItem("ssShowHard") !== "0"; } catch { return true; }
  });
  const [easySet, setEasySet] = useState(() => new Set(MIN_EASY_WORDS));

  // live refs
  const targetGradeRef = useRef(targetGrade);
  const showHardWordsRef = useRef(showHardWords);
  const easySetRef = useRef(easySet);
  useEffect(() => { targetGradeRef.current = targetGrade; }, [targetGrade]);
  useEffect(() => { showHardWordsRef.current = showHardWords; }, [showHardWords]);
  useEffect(() => { easySetRef.current = easySet; }, [easySet]);

  const awardPointsRef = useRef(addScore);
  useEffect(() => { awardPointsRef.current = addScore; }, [addScore]);

  const editor = useEditor({
    extensions: [
      StarterKit.configure({ heading: { levels: [1,2,3,4] } }),
      Placeholder.configure({ placeholder: "Start typing, or paste your text here", includeChildren: true }),
      makeDynamicHighlightsExtension({
        getTargetGrade: () => targetGradeRef.current,
        getShowHardWords: () => showHardWordsRef.current,
        getEasySet: () => easySetRef.current,
        awardPointsRef,
      }),
    ],
    content: "",
    editorProps: {
      attributes: {
        class: "editor-content max-w-none p-4 border rounded bg-white focus:outline-none",
        style: "word-break: normal; overflow-wrap: break-word; hyphens: manual;",
      },
    },
  });

  // DaleChall
  useEffect(() => {
    fetch(process.env.PUBLIC_URL + "/daleChallEasyWords.json")
      .then((r) => (r.ok ? r.json() : []))
      .then((list) => { if (Array.isArray(list) && list.length) setEasySet(new Set(list.map(w => w.toLowerCase()))); })
      .catch(() => {});
  }, []);

  // Recompute when toggles change
  useEffect(() => {
    if (!editor) return;
    const tr = editor.state.tr.setMeta(decoKey, "recompute");
    editor.view.dispatch(tr);
  }, [editor, targetGrade, showHardWords, easySet]);

  // Debounced stats + celebration
  const wasCompleteRef = useRef(false);
  const recalc = useCallback(() => {
    if (!editor) return;
    const text = editor.getText();
    const wc = countWords(text);
    const sc = countSentences(text);
    const grade = calculateFleschKincaidGrade(text);
    const ease = calculateFleschReadingEase(text);
    setStats({ wordCount: wc, sentenceCount: sc, grade, ease });

    const min = targetGradeRef.current;
    const inBand = wc >= MIN_WORDS_FOR_TARGET_BONUS && grade >= min && grade <= min + 0.9;
    if (inBand && !wasCompleteRef.current) {
      wasCompleteRef.current = true;
      addScore(POINTS.target); // big celebration
    } else if (!inBand) {
      wasCompleteRef.current = false;
    }
  }, [editor, addScore]);

  useEffect(() => { wasCompleteRef.current = false; }, [targetGrade]);

  useEffect(() => {
    if (!editor) return;
    let tid = 0;
    const onUpdate = () => {
      // light flavor while typing
      window.dispatchEvent(new CustomEvent("ss-edit-ping"));
      clearTimeout(tid);
      tid = setTimeout(recalc, DEBOUNCE_MS);
    };
    editor.on("update", onUpdate);
    onUpdate();
    return () => { editor.off("update", onUpdate); clearTimeout(tid); };
  }, [editor, recalc]);

  if (!editor) return null;

  const hasText = stats.wordCount > 0 && stats.sentenceCount > 0;
  const gradeDisplay = hasText ? stats.grade.toFixed(1) : "";
  const easeDisplay  = hasText ? stats.ease.toFixed(1)  : "";

  return (
    <div className="space-y-4">
      {/* toolbar */}
      <div className="flex flex-wrap gap-2 mb-2">
        <button onClick={() => editor.chain().focus().toggleBold().run()} className={`px-2 py-1 border rounded ${editor.isActive('bold') ? 'bg-blue-100 border-blue-400' : ''}`}>Bold</button>
        <button onClick={() => editor.chain().focus().toggleItalic().run()} className={`px-2 py-1 border rounded ${editor.isActive('italic') ? 'bg-blue-100 border-blue-400' : ''}`}>Italic</button>

        <button onClick={() => editor.chain().focus().toggleHeading({ level: 1 }).run()} className={`px-2 py-1 border rounded ${editor.isActive('heading', { level: 1 }) ? 'bg-blue-100 border-blue-400' : ''}`}>H1</button>
        <button onClick={() => editor.chain().focus().toggleHeading({ level: 2 }).run()} className={`px-2 py-1 border rounded ${editor.isActive('heading', { level: 2 }) ? 'bg-blue-100 border-blue-400' : ''}`}>H2</button>
        <button onClick={() => editor.chain().focus().toggleHeading({ level: 3 }).run()} className={`px-2 py-1 border rounded ${editor.isActive('heading', { level: 3 }) ? 'bg-blue-100 border-blue-400' : ''}`}>H3</button>
        <button onClick={() => editor.chain().focus().toggleHeading({ level: 4 }).run()} className={`px-2 py-1 border rounded ${editor.isActive('heading', { level: 4 }) ? 'bg-blue-100 border-blue-400' : ''}`}>H4</button>

        <button onClick={() => editor.chain().focus().toggleBulletList().run()} className={`px-2 py-1 border rounded ${editor.isActive('bulletList') ? 'bg-blue-100 border-blue-400' : ''}`}> List</button>
        <button onClick={() => editor.chain().focus().toggleOrderedList().run()} className={`px-2 py-1 border rounded ${editor.isActive('orderedList') ? 'bg-blue-100 border-blue-400' : ''}`}>1. List</button>

        <button onClick={() => editor.chain().focus().clearNodes().unsetAllMarks().run()} className="px-2 py-1 border rounded" title="Clear formatting">Clear</button>
      </div>

      {/* legend */}
      <div className="flex flex-wrap gap-4 text-xs mb-2 items-center">
        <div className="flex items-center gap-1">
          <span className="legend-yellow w-3 h-3 border inline-block"></span>
          <span>Hard to Read</span>
        </div>
        <div className="flex items-center gap-1">
          <span className="legend-red w-3 h-3 border inline-block"></span>
          <span>Very hard to read</span>
        </div>
        {ENABLE_BLUE && (
          <div className="flex items-center gap-1">
            <span className="legend-blue w-3 h-3 border inline-block"></span>
            <span>Long sentence</span>
          </div>
        )}
        <div className="flex items-center gap-2">
          <span className="legend-underline-gray" aria-hidden="true"></span>
          <span>Style suggestion</span>
        </div>
        <div className="flex items-center gap-2 ml-auto">
          <label className="inline-flex items-center gap-1">
            <input type="checkbox" checked={showHardWords} onChange={(e) => setShowHardWords(e.target.checked)} />
            <span className="legend-underline-purple">Likely hard word</span>
          </label>
        </div>
      </div>

      {/* stats */}
      <div className="flex flex-wrap gap-4 text-sm">
        <div className="bg-gray-100 px-2 py-1 rounded">Words, {stats.wordCount}</div>
        <div className="bg-gray-100 px-2 py-1 rounded">Sentences, {stats.sentenceCount}</div>
        <div className="bg-green-100 px-2 py-1 rounded">Grade, {gradeDisplay}</div>
        <div className="bg-blue-100 px-2 py-1 rounded">Ease, {easeDisplay}</div>
      </div>

      <div className="editor-frame">
        <EditorContent editor={editor} />
      </div>
    </div>
  );
}

/* seed */
const MIN_EASY_WORDS = [
  "a","about","after","all","also","an","and","any","are","as","at","back","be","because","been","before","but","by",
  "can","come","could","day","did","do","down","each","even","every","few","find","first","for","from","get","go",
  "good","had","has","have","he","her","here","him","his","how","i","if","in","into","is","it","just","know","like",
  "little","long","look","made","make","man","many","may","me","men","more","most","much","must","my","new","no",
  "not","now","of","off","old","on","one","only","or","other","our","out","over","people","right","said","see","she",
  "so","some","than","that","the","their","them","then","there","these","they","this","time","to","two","up","us",
  "use","very","want","was","way","we","well","went","were","what","when","where","which","who","will","with","work",
  "would","year","years","you","your"
];
