import React, { useEffect, useRef, useState } from "react";
import { useArcade } from "../ArcadeEngine";

/**
 * Effects layer:
 *  - Reacts to score bumps (lastDelta/pingAt) with shake/glow/confetti.
 *  - Also listens to "ss-edit-ping" for a subtle glow while typing.
 *  - For >= +1000, shows banner + big confetti.
 */
export default function ScreenFX({ enabled = false }) {
  const { lastDelta, pingAt } = useArcade();

  const [celebrate, setCelebrate] = useState(false);
  const [pieces, setPieces] = useState([]);
  const cleanupTimer = useRef(null);
  const editPingAtRef = useRef(0);

  const clearTimers = () => {
    if (cleanupTimer.current) {
      clearTimeout(cleanupTimer.current);
      cleanupTimer.current = null;
    }
  };
  const clearBody = () => {
    document.body.classList.remove(
      "arcade-shake-big",
      "arcade-shake-small",
      "arcade-glow"
    );
  };

  useEffect(() => () => { clearTimers(); clearBody(); }, []);

  const makePieces = (count, topStart = -8) => {
    const arr = [];
    for (let i = 0; i < count; i++) {
      arr.push({
        id: `${Date.now()}-${i}`,
        left: Math.random() * 100,
        delay: Math.random() * 0.2,
        dur: 0.7 + Math.random() * 0.9,
        rot: Math.floor(Math.random() * 360),
        top: topStart + Math.random() * 4,
      });
    }
    return arr;
  };

  const micro = (prefer = null) => {
    // flash removed on purpose  only shake / glow / confetti
    const choices = ["shake", "glow", "confetti"];
    const kind = prefer || choices[Math.floor(Math.random() * choices.length)];

    if (kind === "shake") document.body.classList.add("arcade-shake-small");
    if (kind === "glow")  document.body.classList.add("arcade-glow");
    if (kind === "confetti") setPieces(makePieces(40));

    cleanupTimer.current = setTimeout(() => {
      setPieces([]);
      clearBody();
    }, kind === "confetti" ? 1000 : 700);
  };

  // Big celebration on +1000
  useEffect(() => {
    if (!enabled || !pingAt || lastDelta <= 0) return;

    clearTimers();
    clearBody();

    if (lastDelta >= 1000) {
      setCelebrate(true);
      setPieces(makePieces(180));
      document.body.classList.add("arcade-shake-big");
      cleanupTimer.current = setTimeout(() => {
        setCelebrate(false);
        setPieces([]);
        clearBody();
      }, 1500);
      return;
    }

    // Micro effect for smaller bumps
    micro();
  }, [enabled, lastDelta, pingAt]);

  // Subtle glow while typing (no score change required)
  useEffect(() => {
    const onEditPing = () => {
      const now = Date.now();
      if (now - editPingAtRef.current < 600) return; // throttle 0.6s
      editPingAtRef.current = now;
      if (!enabled) return;
      micro("glow");
    };
    window.addEventListener("ss-edit-ping", onEditPing);
    return () => window.removeEventListener("ss-edit-ping", onEditPing);
  }, [enabled]);

  const showOverlay = celebrate || pieces.length > 0;
  if (!showOverlay) return null;

  return (
    <div className="fx-overlay" aria-hidden>
      {celebrate && <div className="fx-banner">TARGET REACHED</div>}
      <div className="fx-confetti-wrap">
        {pieces.map((p) => (
          <span
            key={p.id}
            className="fx-piece"
            style={{
              left: `${p.left}%`,
              top: `${p.top}%`,
              animationDelay: `${p.delay}s`,
              animationDuration: `${p.dur}s`,
              transform: `rotate(${p.rot}deg)`,
            }}
          />
        ))}
      </div>
    </div>
  );
}
