import React, {
  createContext,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { createPortal } from "react-dom";

const ArcadeCtx = createContext(null);

export function useArcade() {
  const ctx = useContext(ArcadeCtx);
  if (!ctx) throw new Error("useArcade must be used inside <ArcadeProvider>");
  return ctx;
}

export function ArcadeProvider({ children }) {
  const [score, setScore] = useState(0);

  // Effects layer reads these to know a new bump happened.
  const [lastDelta, setLastDelta] = useState(0);
  const [pingAt, setPingAt] = useState(0);

  // Neon HUD subscribes to bumps for its own sparkles.
  const listenersRef = useRef(new Set());

  const addScore = (delta = 0) => {
    if (!Number.isFinite(delta) || delta <= 0) return;
    setScore((s) => {
      const next = s + delta;
      setLastDelta(delta);
      setPingAt(Date.now());
      listenersRef.current.forEach((fn) => { try { fn(delta, next); } catch {} });
      return next;
    });
  };
  const resetScore = () => setScore(0);

  const value = useMemo(
    () => ({
      score,
      addScore,
      resetScore,
      lastDelta,
      pingAt,
      _subscribe(fn) {
        listenersRef.current.add(fn);
        return () => listenersRef.current.delete(fn);
      },
    }),
    [score, lastDelta, pingAt]
  );

  return (
    <ArcadeCtx.Provider value={value}>
      {children}
      <ArcadeHUD />
    </ArcadeCtx.Provider>
  );
}

/* ---------------- HUD (neon scoreboard) ---------------- */

function ArcadeHUD() {
  const { score, resetScore, _subscribe } = useArcade();
  const [rootEl, setRootEl] = useState(null);
  const [isArcadeTheme, setIsArcadeTheme] = useState(false);

  useEffect(() => {
    const update = () => setIsArcadeTheme(document.body.classList.contains("theme-arcade"));
    update();
    const obs = new MutationObserver(update);
    obs.observe(document.body, { attributes: true, attributeFilter: ["class"] });
    return () => obs.disconnect();
  }, []);

  useEffect(() => {
    const el = document.createElement("div");
    el.id = "arcade-portal-root";
    document.body.appendChild(el);
    setRootEl(el);
    return () => el.remove();
  }, []);

  if (!rootEl || !isArcadeTheme) return null;
  return createPortal(
    <HUDInner score={score} resetScore={resetScore} subscribe={_subscribe} />,
    rootEl
  );
}

function HUDInner({ score, resetScore, subscribe }) {
  const [displayScore, setDisplayScore] = useState(score);
  const [pulse, setPulse] = useState(false);
  const [glitch, setGlitch] = useState(false);
  const [floaters, setFloaters] = useState([]);
  const [particles, setParticles] = useState([]);
  const rafRef = useRef();
  const lastAnimRef = useRef(0);
  const prevScoreRef = useRef(score);

  // Smooth count-up
  useEffect(() => {
    const start = performance.now();
    const from = displayScore;
    const to = score;
    const duration = 600;
    cancelAnimationFrame(rafRef.current);
    const tick = (t) => {
      const p = Math.min(1, (t - start) / duration);
      const eased = 1 - Math.pow(1 - p, 3);
      setDisplayScore(Math.round(from + (to - from) * eased));
      if (p < 1) rafRef.current = requestAnimationFrame(tick);
    };
    rafRef.current = requestAnimationFrame(tick);
    return () => cancelAnimationFrame(rafRef.current);
  }, [score, displayScore]);

  const triggerFX = (delta) => {
    if (!delta || delta <= 0) return;
    setPulse(true); setTimeout(() => setPulse(false), 500);
    setGlitch(true); setTimeout(() => setGlitch(false), 350);

    const id = Math.random().toString(36).slice(2);
    setFloaters((a) => [...a, { id, text: `+${delta}`, x: Math.random() * 28 - 14 }]);
    setTimeout(() => setFloaters((a) => a.filter((f) => f.id !== id)), 900);

    const count = 12;
    const newParts = Array.from({ length: count }).map(() => {
      const angle = Math.random() * Math.PI * 2;
      const dist = 60 + Math.random() * 40;
      return {
        id: Math.random().toString(36).slice(2),
        hue: 180 + Math.random() * 120,
        tx: Math.cos(angle) * dist,
        ty: Math.sin(angle) * dist,
        scale: 0.6 + Math.random() * 0.9,
        rot: (Math.random() * 60 - 30).toFixed(1),
      };
    });
    setParticles((p) => [...p, ...newParts]);
    setTimeout(() => setParticles((p) => p.filter((x) => !newParts.includes(x))), 900);

    lastAnimRef.current = Date.now();
  };

  useEffect(() => subscribe((delta) => triggerFX(delta)), [subscribe]);

  // Fallback if subscriber missed
  useEffect(() => {
    const delta = score - prevScoreRef.current;
    prevScoreRef.current = score;
    if (delta > 0 && Date.now() - lastAnimRef.current > 100) triggerFX(delta);
  }, [score]);

  return (
    <div className="arcade-hud">
      <div className={`score-hud ${pulse ? "is-pulse" : ""} ${glitch ? "is-glitch" : ""}`}>
        <div className="score-glow" aria-hidden="true" />
        <div className="score-label">SCORE</div>
        <div className="score-value" aria-live="polite">{displayScore}</div>

        {floaters.map((f) => (
          <div key={f.id} className="score-floater" style={{ transform: `translateX(${f.x}px)` }}>
            {f.text}
          </div>
        ))}

        <div className="score-particles" aria-hidden="true">
          {particles.map((p) => (
            <span
              key={p.id}
              className="particle"
              style={{
                "--tx": `${p.tx}px`,
                "--ty": `${p.ty}px`,
                "--rot": `${p.rot}deg`,
                "--scale": p.scale,
                "--hue": p.hue,
              }}
            />
          ))}
        </div>

        <button className="score-reset" onClick={resetScore} title="Reset score">Reset</button>
      </div>
    </div>
  );
}

export default ArcadeProvider;
